<template>
  <video id="player" controls></video>
</template>

<script>
import { mapGetters } from "vuex";
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

let video = null;

export default {
  data() {
    return {
      window_width: 0,
      window_height: 0
    };
  },
  computed: {
    ...mapGetters(["getHlsUrl"])
  },
  props: ["props_tab"],
  created() {
    this.$store.dispatch("LOAD_HLS_URL").then(() => {
      console.log("#####################");
      console.log(this.getHlsUrl);
      console.log("#####################");
      this.$refs.video_source.setAttribute("src", this.getHlsUrl);

      video = window.videojs("hls-example");
    });

    // setTimeout(() => {
    //   this.playVideo();
    // }, 1000);

    EventBus.$on(EVENT_SET_EVENT_TYPE, path => {
      if (path === "/event/live") {
        // setTimeout(() => {
        //   this.playVideo();
        // }, 2000);
      }
    });

    this.$nextTick(() => {
      const Hls = window.Hls;
      if (Hls.isSupported()) {
        var video = document.getElementById("player");
        console.log(video)

        var hls = new Hls();
        // bind them together
        hls.attachMedia(video);
        hls.on(Hls.Events.MEDIA_ATTACHED, function() {
          console.log("video and hls.js are now bound together !");
          hls.loadSource("http://221.147.43.26:1935/live/2/playlist.m3u8");
          hls.on(Hls.Events.MANIFEST_PARSED, function(event, data) {
            console.log(
              "manifest loaded, found " + data.levels.length + " quality level"
            );
          });
        });
      }
    });
  },
  methods: {
    playVideo() {
      console.log("!!!!!!");
      video.play();
    }
  }
};
</script>

<style>
#hls-example {
  background-color: black;
  width: 100vw;
}
</style>
