<template>
  <div>
    INFO
    <form>
      <v-text-field :counter="10" label="Name" required></v-text-field>
      <v-text-field label="E-mail" required></v-text-field>

      <v-btn class="mr-4" @click="submit">
        submit
      </v-btn>
      <v-btn @click="clear">
        clear
      </v-btn>
    </form>
    <button @click="gotoPreset">TO PRESET</button>
  </div>
</template>

<script>
export default {
  methods: {
    gotoPreset() {
      this.$router.push("/event/preset");
    },
    submit() {
      this.$v.$touch();
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.select = null;
      this.checkbox = false;
    }
  }
};
</script>

