<template>
  <div>
    <div class="root">
      <HLSVideo :style="{ display: is_video_show }"></HLSVideo>
      <router-view class="contents"></router-view>
    </div>
    <div :class="sender_class">
      <WebRTCSender :width="sender_width" :height="sender_height" />
    </div>
  </div>
</template>

<script>
import HLSVideo from "@/components/HLSVideo.vue";
import WebRTCSender from "@/components/WebRTCSender.vue";
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

export default {
  data() {
    return {
      is_video_show: "none",
      sender_class: "preset_sender",
      sender_width: 0,
      sender_height: 0,
      sender_size: 0
    };
  },
  components: {
    WebRTCSender,
    HLSVideo
  },
  created() {
    EventBus.$on(EVENT_SET_EVENT_TYPE, () => {
      this.setLiveStatus();
    });

    console.log(this.$router.history.current.path);

    const size = window.innerWidth;
    this.sender_width = size;
    this.sender_height = size;

    if (this.$router.history.current.path === "/event/live") {
      console.log("setLiveStatus");
      this.setLiveStatus();
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    gotoHome() {
      this.$router.push("/info");
    },
    setLiveStatus() {
      console.log("!!!!");
      this.is_video_show = "block";
      this.sender_class = "live_sender";
      const video_height = (window.innerWidth * 9) / 16;
      this.sender_width = (video_height * 9) / 16;
      this.sender_height = video_height;
      console.log(video_height, window.innerWidth);
      console.log(this.sender_width, this.sender_height);
    }
  }
};
</script>

<style scoped>
.contents {
  width: 100%;
}

.preset_sender {
  /* background-color: red; */
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.live_sender {
  position: absolute;
  display: flex;
  right: 0;
  /* align-content: flex-start;
  justify-items: flex-start; */
}

.root {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>