<template>
  <div class="root">
    <div class="title" :style="{ height: option_height }">
      장비의 상태를 확인하세요.
    </div>
    <div class="option" :style="{ height: option_height }">
      <v-btn rounded outlined class="btn ok_btn" @click="gotoLive">
        입장
      </v-btn>
      <v-btn rounded outlined class="btn cancle_btn" @click="gotoHome">
        취소
      </v-btn>
    </div>
  </div>
</template>

<script>
import { publishVideo } from "../utils/webrtc/publish";

export default {
  data() {
    return {
      option_height: "0px"
    };
  },
  created() {
    this.$nextTick(() => {
      const height = window.innerHeight - window.innerWidth;
      this.option_height = height * 0.5 + "px";
    });
  },
  methods: {
    gotoLive() {
      console.log("PUBLISH");
      publishVideo();

      this.$router.push("/event/live");
    },
    gotoHome() {
      this.$router.push("/");
    }
  }
};
</script>

<style scoped>
.root {
  position: absolute;
  width: 100%;
  height: 100%;
}

.title {
  text-align: center;
  font-size: 22px;
  font-weight: bold;
  color: #3269cd;
  display: flex;
  justify-content: center;
  align-items: center;
}

.option {
  position: absolute;
  width: 100%;
  bottom: 0;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.btn {
  margin: 0px 5px;
  width: 100px;
  height: 20px;
  font-size: 15px;
  font-weight: bold;
}

.ok_btn {
  background-color: #3269cd;
  color: white;
}

.cancle_btn {
  color: #3269cd;
}
</style>